@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
    --primary-blue: #163ead;
    --secondary-blue: #1e4ed4;
    --grey-1: #112d48;
    --grey-2: #273e58;
    --grey-3: #314d67;
    --grey-4: #47627c;
    --grey-5: #607c97;
    --grey-6: #8299af;
    --grey-7: #9cb0c5;
    --grey-8: #becddc;
    --grey-9: #d5dee9;
    --grey-10: #edf2f6;
    --grey-11: #efefef;
    --grey-12: #f5f3f3;
    --grey-13: #f9f6f6;
    --white: #fff;
}
* {
    margin: 0;
    padding: 0;
}

*,
*::before,*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;
    outline: none;
}

body {
    font-family: "Roboto", sans-serif;
    font-size: 1.5rem;
}

body::-webkit-scrollbar {
    width: 0.5rem;
}
body::-webkit-scrollbar {
    scroll-behavior: smooth;
    width: 6px;
}
body::-webkit-scrollbar-thumb {
    background: rgb(56, 147, 163);
    border-radius: 1.8rem;
}

body::-webkit-scrollbar-thumb:hover {
    background: #1e4ed4;
    background: var(--secondary-blue);
}


body::-webkit-scrollbar-track {
    background: #d5dee9;
    background: var(--grey-9);
}

@media only screen and (max-width: 37.5em) {
    html {
        font-size: 56.5%;
    }
}

button {
    background: none;
    cursor: pointer;
}

h1 {
    font-size: 6.4rem;
}

h2 {
    font-size: 4rem;
}

h3 {
    font-size: 2.8rem;
}

h4 {
    font-size: 1.6rem;
}
h5 {
    font-size: 1.2rem;
}
/* p, span {
    font-size: small;
} */

h1,h2,h3,h4,h5 {
    line-height: 1;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: #112d48;
    color: var(--grey-1);
    cursor: pointer;
}

@media only screen and (max-width: 50em) {
    h1,h2,h3,h4 {
        letter-spacing: var(--spacing);
        text-transform: capitalize;
        line-height: 1.25;
    }
    h4 {
        font-size: 1.5rem;
    }
    h3 {
        font-size: 2.1rem;
    }
    h2 {
        font-size: 3.3rem;
    }
    h1 {
        font-size: 4.5rem;
    }
}

.bg-color-icon {
    fill: slategray;
}
.bg-color-status-icon {
    fill: #1e4ed4;
    fill: var(--secondary-blue);
}

.flex-gap {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
}
.border-curve {
    border: 1px solid #d5dee9;
    border: 1px solid var(--grey-9);
    border-radius: 5px;
    box-shadow: 0px 0px 2px 2px #f3f3f3
}
.flex-align-center {
    display: flex;
    align-items: center;
}

.pointer {
    cursor: pointer;
}

.card-btn{
    color: #3f3d3d9c;
    font-weight: 402;
    border-radius: 5px;
    padding: 0.3rem;
    font-size: 1.3rem;
    background-color: #fdfdfd;
    border: 1px solid #f3efef;
}
.card-container {
    padding: 1.2rem;
    background-color: #fefefe;
    width: 100%;
    max-width: 50rem;
}
.card-container .card-title {
    font-size: 1.5rem;
    color: #3f3d3ded;
    font-weight: 402;
    width: 100%;
    margin-right: 2rem;
    max-width: 95%;
    padding: 0.5rem 0;
}
.card-container .card-tag-container {
    grid-gap: 1rem;
    gap: 1rem;
    width: 100%;
    max-width: -webkit-max-content;
    max-width: max-content;
    height: -webkit-max-content;
    height: max-content;
    display: flex;
}
.card-container .card-tag-container .card-tag {
    grid-gap: 0.5rem;
    gap: 0.5rem;
    max-width: -webkit-max-content;
    max-width: max-content;
    display: flex;
    height: 100%;
    align-items: center;
}
.card-container .card-tag-container .alert-icon {
    /* padding: 2.8px 5px; */
    align-self: center;
    height: 100%;
}

.card-container .card-header {
    align-items: center;
    justify-content: space-between;
    display: flex;
}
.card-container .card-hero {
    max-width: 100%;
    width: 100%;
}
.border-curve-card{
    box-shadow: 0px 0px 2px 2px #eef0f1;
    border-radius: 7px;
}
.avatar-container {
    position: relative;
    display: inline-block;
}
.card-id{
    color: #3f3d3d9c;
    font-weight: 402;
    font-size: 1.5rem;
}
.card-image-container {
    width: 2.5rem;
    border-radius: 50%;
    height: 2.4rem;
    position: relative;
    display: flex;
}
.card-image-container .image {
    width: 100%;
    border-radius: 50%;
    height: unset;
    position: relative;
    object-fit: contain;
}
.card-avatar-badge {
    position: absolute;
    display: inline-block;
    border: 2px solid white;
    width: 0.74rem;
    height: 0.74rem;
    border-radius: 50%;
    background: lightgray;
    bottom: 0;
    right: 0;
}
.card-avatar-badge-available {
    position: absolute;
    display: inline-block;
    border: 2px solid white;
    width: 0.74rem;
    height: 0.74rem;
    border-radius: 50%;
    bottom: 0;
    right: 0;
    background: green;
}

.main-panel-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 100%;
    padding: 0.6rem;
    width: 100%;
    max-width: 100%;
}
.p-image{
    border-radius: 50%;
}
.main-panel-container .panel-header {
    justify-content: space-between;
    margin: 0 0 20px 0;
    display: flex;
    padding-inline: 2.6px;
}
.card-avatar-badge .available {
    background: green;
}
.panel-top-btn{
    border: none;
}
.main-panel-container .panel-card {
    grid-gap: 1rem;
    gap: 1rem;
    display: grid;
}

header {
    padding: 1rem 2rem;
    border-bottom: 1px solid var(--grey-9);
}

.content-container {
    min-height: 100vh;
    background-color: var(--grey-13);
    background-color: #f9f9f9;
}
.display-button {
    padding: 0.4rem 1rem;
    display: flex;
    grid-gap: 0.4rem;
    gap: 0.4rem;
    align-items: center;
}

.nav-container {
    max-width: -webkit-max-content;
    max-width: max-content;
}

.flex-container {
    padding: 5px;
    display: flex;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
}

.Panel-box-container {
    padding: 2.5rem;
}

.Panel-box-inner {
    display: grid;
    grid-gap: 9px;
    gap: 9px;
    height: 100%;
    max-height: 100%;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.select-element {
    text-transform: capitalize;
    display: inline-block;
    border-radius: 5px;
    position: relative;
    width: 15rem;
    cursor: pointer;
    padding: 0.5rem;
}

.select-popup {
    max-width: -webkit-max-content;
    max-width: max-content;
    z-index: 100000;
    background-color: white;
    margin-top: 0.3rem;
    position: absolute;
    padding: 1rem;
}

.select-popup .flex-container {
    align-items: center;
}

.show-modal {
    display: block;
}

.hide-modal {
    display: none;
}

@media screen and (max-width: 1000px) {
    .Panel-box-inner {
        grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    }
}

